@import url('https://fonts.googleapis.com/css2?family=Short+Stack&display=swap');
.board{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.board .boxBoard{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 10;
    background-color: transparent;
}
.boxBoard .boardrow{
    display: flex;
}

.board .heading{
    position: absolute;
    top: 15%;
    font-size: 2rem;
    font-family: 'Short Stack';
    font-weight: bold;
}
.board .restartBtn{
    font-size: 1.4rem;
    letter-spacing: 2px;
    padding: 10px;
    font-weight: bolder;
    position: absolute;
    bottom: 8%;
    text-transform: uppercase;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: #1645c7;
    border: none;
    border-radius: 10px;
    color: white;
    width: 150px;
    box-shadow: 0px 5px 10px #00000044;
}
.gifAnimation{
    position: absolute;
    top: 0%;
}
.drawicon{
    position: absolute;
    top: 10%;
}