body{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: hidden;
}
.nav{
    position: absolute;
    top: 0;
    left : 0;
    width: 100vw;
    padding: 15px;
    font-size: 1.5rem;
    font-weight: bolder;
    color: #000000;
    z-index: 100;
    background-color: #fff;
}
.icon{
    display: inline;
    vertical-align: middle;
}
.line{
    width: 120%;
    height: 3px;
    background: linear-gradient(135deg, blue,red);
    position: absolute;
    top: 70px;
    left: 0;
    z-index: 100;
}
